@charset "UTF-8";
/*! mobile 버전 - css */
/*! pc & tablet 버전 - css */
.pgheader_wrap {
  min-width: 1280px;
  height: 60px;
  background: #a675fe;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 100; }

.pgheader_in {
  position: relative; }

.pghleft_w {
  height: 60px;
  position: relative;
  width: 250px;
  box-sizing: border-box;
  padding: 0 60px;
  overflow: hidden; }
  .pghleft_w .btn_pghtotal {
    width: 30px;
    height: 30px;
    display: block;
    position: absolute;
    top: 20px;
    left: 20px; }
    .pghleft_w .btn_pghtotal .bar {
      position: absolute;
      left: 0;
      width: 22px;
      height: 2px;
      background: #333;
      transform: rotate(0); }
      .pghleft_w .btn_pghtotal .bar:nth-child(1) {
        top: 0;
        transform-origin: 0 0; }
      .pghleft_w .btn_pghtotal .bar:nth-child(2) {
        top: 8px; }
      .pghleft_w .btn_pghtotal .bar:nth-child(3) {
        top: 16px;
        transform-origin: 0 100%; }
    .pghleft_w .btn_pghtotal.on .bar:nth-child(1) {
      top: 1px;
      left: 3px;
      transform: rotate(45deg); }
    .pghleft_w .btn_pghtotal.on .bar:nth-child(2) {
      opacity: 0; }
    .pghleft_w .btn_pghtotal.on .bar:nth-child(3) {
      left: 3px;
      transform: rotate(-45deg); }

.pgh_title {
  display: flex;
  height: 60px;
  box-sizing: border-box;
  align-items: center;
  line-height: 1.3;
  justify-content: center;
  font-size: 21px;
  font-family: 'NanumSquare';
  font-weight: 700;
  color: #fff;
  white-space: nowrap; }

.adm_men_w {
  display: inline-block;
  position: absolute;
  top: 24px;
  right: 40px; }

.adm_mendl_w {
  position: relative;
  display: block;
  padding: 0 21px 0 30px;
  font-size: 0;
  line-height: 0; }
  .adm_mendl_w .adm_men {
    display: inline-block;
    width: 30px;
    height: 30px;
    background: #13d5aa url("../images/admin/ico_logtop.png") no-repeat 50% 0/cover;
    border-radius: 100%; }
  .adm_mendl_w .adm_men_name {
    display: inline-block;
    font-size: 12px;
    color: #fff;
    line-height: 1;
    white-space: nowrap; }
  .adm_mendl_w::before {
    content: '';
    position: absolute;
    top: -9px;
    left: 0;
    display: block;
    width: 30px;
    height: 30px;
    background: url("../images/admin/ico_logtop.png") no-repeat 0 0; }
  .adm_mendl_w:after {
    content: '';
    position: absolute;
    top: 50%;
    right: 3px;
    display: block;
    width: 8px;
    height: 8px;
    margin-top: -8px;
    border-right: 1px solid #fff;
    border-bottom: 1px solid #fff;
    transform: translateZ(0);
    transform: rotate(45deg); }

.mentype_list_w {
  position: absolute;
  top: 25px;
  right: 0;
  display: none;
  min-width: 100%;
  background: #fff;
  box-shadow: 1px 0px 15px -2px rgba(0, 0, 0, 0.2); }
  .mentype_list_w .mentype_list {
    padding: 5px 0; }
    .mentype_list_w .mentype_list .mentype {
      display: block;
      padding: 5px 15px;
      line-height: 1.3;
      white-space: nowrap; }
